___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 90vw;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.author {
    font-size: 0.9rem;
    color: #333;
    background-color: #fff;
    position: fixed;
    padding: 5px;
    border-radius: 10px 0px 0px 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    right: 0;
    bottom: 10px;

    text-decoration: none;
}

.location {
    width: 120px;
    text-align: center;
    font-weight: bold;
    padding: 5px 10px;
    border: 2px solid #f2f2f2;
    border-radius: 5px;
    font-size: 25px;
    color: #333333;
    transition: all 0.3s ease;
}

.location:hover {
    border-color: #666666;
}

.location:focus {
    outline: none;
    border-color: #333333;
    box-shadow: 0 0 0 2px #333333;
}

.day {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h2 {
    margin: 0px;
    padding: 10px 0 0 0;
}

.location-info {
    color: #333;
    background-color: #fff;
    text-align: center;
    position: sticky;
    top: -1px;
    z-index: 2;
}

.date {
    width: 100%;
    color: #333;
    background-color: #fff;
    text-align: center;
    position: sticky;
    top: 2.2rem;
    z-index: 1;
}

.weather-table {
    width: calc(100% - 5px);
    border-collapse: collapse;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.weather-table th,
.weather-table td {
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

.weather-table th {
    background-color: #f9f9f9;
    color: #333;
    padding: 10px;
    position: sticky;
    top: 4.5rem;
}

.weather-table td {
    background-color: #fff;
    padding: 0 10px;
}

.weather-table tr:nth-child(even) td {
    background-color: #f2f2f2;
}

.condition-icon {
    height: 40px;
    padding: 0 10px;
}

.weather-table .temp {
    color: #FF5722;
}

.weather-table .humidity {
    color: #2196F3;
}

.weather-table .condition {
    color: #9C27B0;
}

.weather-table .wind {
    color: #4CAF50;
}

.weather-table .rain-chance {
    color: #FFC107;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,8BAA8B;IAC9B,yBAAyB;IACzB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uCAAuC;IACvC,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,sBAAsB;IACtB,eAAe;IACf,YAAY;IACZ,gCAAgC;IAChC,uCAAuC;IACvC,QAAQ;IACR,YAAY;;IAEZ,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,yBAAyB;IACzB,kBAAkB;IAClB,eAAe;IACf,cAAc;IACd,yBAAyB;AAC7B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,6BAA6B;AACjC;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,WAAW;IACX,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;IACX,UAAU;AACd;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;;IAEI,kBAAkB;IAClB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,WAAW;IACX,aAAa;IACb,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,sBAAsB;IACtB,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB","sourcesContent":["body {\n    font-family: Arial, sans-serif;\n    background-color: #f4f4f4;\n    margin: 0;\n    padding: 0;\n}\n\n.container {\n    max-width: 90vw;\n    margin: 20px auto;\n    padding: 20px;\n    background-color: #fff;\n    border-radius: 10px;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.author {\n    font-size: 0.9rem;\n    color: #333;\n    background-color: #fff;\n    position: fixed;\n    padding: 5px;\n    border-radius: 10px 0px 0px 10px;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\n    right: 0;\n    bottom: 10px;\n\n    text-decoration: none;\n}\n\n.location {\n    width: 120px;\n    text-align: center;\n    font-weight: bold;\n    padding: 5px 10px;\n 